import React from 'react';
import styles from './Instructions.module.css';

const Instructions: React.FC = () => {
  return (
    <div className={styles.instructionsContainer}>
      <div className={styles.instructionsContentWrapper}>
        <h1 className={styles.instructionsTitle}>
          Uputstva
        </h1>
        <div className={styles.instructionsSection}>
          <p className={styles.instructionsParagraph}>
            <strong>Slagalica</strong> je digitalna verzija popularnog televizijskog kviza koji se ve decenijama prikazuje u Srbiji i regionu. 
            Emisija je poznata po tome to testira znanje, logiku, snalaljivost i brzinu razmiljanja kroz niz razliitih i uzbudljivih igara.
          </p>
          <p className={styles.instructionsParagraph}>
            Ova verzija Slagalice omoguava igraima da kroz zabavan i izazovan format vjebaju razliite vrste vjetina  od jezikih i matematikih, do geografskih i logikih.
          </p>
          <p className={styles.instructionsParagraph}>
            U okviru ove aplikacije dostupno je <strong>10 razliitih igara</strong>. Svaka igra donosi novi tip izazova, osmiljen tako da aktivira razliite oblike razmiljanja i snalaljivosti. 
            Neke igre su vremenski ograniene, dok druge zahtijevaju precizno povezivanje pojmova, brzo raunanje ili bogato opte znanje.
          </p>
        </div>
        <SlagalicaSection />
        <MojBrojSection />
        <SpajalicaSection />
        <ParoviSection />
        <DesnoLijevoSection />
        <SefSection />
        <ZidSection />
        <PutOkoSvijetaSection />
        <AsocijacijeSection />
        <PremetaljkaSection />
      </div>
    </div>
  );
};

const SlagalicaSection: React.FC = () => (
    <div className={styles.instructionsSection}>
      <h2 className={styles.subTitle}>Slagalica</h2>
      <p className={styles.instructionsParagraph}>
        Slagalica je klasina igra slaganja rijei u kojoj je cilj od ponuenih slova sastaviti najduu moguu rije. 
        Ova igra testira igraevo znanje jezika, brzinu razmiljanja i sposobnost kombinovanja slova.
      </p>
      <p className={styles.instructionsParagraph}>
        Na poetku igre, prikazuje se 12 nasumino odabranih slova. Igra ima <strong>90 sekundi</strong> da unese rije koja koristi ta slova.
        Slova se mogu koristiti samo onoliko puta koliko se pojavljuju u ponuenom skupu.
      </p>
      <p className={styles.instructionsParagraph}>
        <strong>Pravila bodovanja:</strong><br />
        <ul>
          <li>Za svako ispravno slovo u rijei, igra dobija <strong>2 poena</strong>. </li>
          <li>Ako je unijeta rije dua od rijei koju je kompjuter pronaao kao najduu, igra dobija dodatni <strong>bonus od 6 poena</strong>.</li>
        </ul>
      </p>
    </div>
)

const MojBrojSection: React.FC = () => {
  return (
    <div className={styles.instructionsSection}>
      <h2 className={styles.subTitle}> Moj Broj</h2>
      <p className={styles.instructionsParagraph}>
        Moj Broj je matematika igra u kojoj je cilj doi do zadatog broja koristei ponuene brojeve i osnovne matematike operacije:
        sabiranje, oduzimanje, mnoenje i dijeljenje.
      </p>
      <p className={styles.instructionsParagraph}>
        Na poetku igre igrau se nasumino dodijeli 6 brojeva i jedan ciljani broj. Koristei ponuene brojeve, igra pokuava
        da izrauna ciljani broj ili to blii mogui rezultat, u vremenskom roku od <strong>90 sekundi</strong>.
      </p>
      <p className={styles.instructionsParagraph}>
        Svaki broj iz ponude moe se koristiti najvie jednom. Do rezultata se moe doi bilo kojom kombinacijom dozvoljenih operacija,
        potujui matematika pravila i redoslijed izvoenja.
      </p>
      <p className={styles.instructionsParagraph}>
        <strong>Bodovanje:</strong>
      </p>
      <ul className={styles.instructionsParagraph}>
        <li><strong>30 poena</strong> za tano pogoen broj</li>
        <li><strong>20 poena</strong> ako je razlika  5</li>
        <li><strong>10 poena</strong> ako je razlika  10</li>
        <li><strong>5 poena</strong> ako je razlika  20</li>
        <li><strong>0 poena</strong> u svim ostalim sluajevima</li>
      </ul>
    </div>
  );
};

const SpajalicaSection: React.FC = () => {
  return (
    <div className={styles.instructionsSection}>
      <h2 className={styles.subTitle}>Spajalica</h2>
      <p className={styles.instructionsParagraph}>
        Spajalica je igra u kojoj je cilj da igra povee pojmove iz dvije kolone tako da zajedno ine logike parove.
        U svakoj igri prikazuje se po 8 pojmova sa lijeve i 8 pojmova sa desne strane.
      </p>
      <p className={styles.instructionsParagraph}>
        Igra treba da pronae i spoji svih 8 tanih parova u vremenskom roku od <strong>90 sekundi</strong>.
        Pojmovi mogu biti povezani tematski, znaenjski, gramatiki ili na osnovu opteg znanja.
      </p>
      <p className={styles.instructionsParagraph}>
        <strong>Bodovanje:</strong>
      </p>
      <ul className={styles.instructionsParagraph}>
        <li><strong>4 poena</strong> za svaki tano spojen par</li>
      </ul>
    </div>
  );
};

const ParoviSection: React.FC = () => {
  return (
    <div className={styles.instructionsSection}>
      <h2 className={styles.subTitle}>Parovi</h2>
      <p className={styles.instructionsParagraph}>
        Parovi je jednostavna memorijska igra u kojoj je cilj pronai svih <strong>8 parova sliica</strong>.
        Igra na poetku igre ima <strong>12 pokuaja</strong> da otkrije sve parove.
      </p>
      <p className={styles.instructionsParagraph}>
        U svakom pokuaju igra otkriva dvije sliice:
      </p>
      <ul className={styles.instructionsParagraph}>
        <li>Ako su sliice iste, smatra se da je pronaen par i one ostaju otkrivene. Igra ne gubi pokuaj i osvaja <strong>2 poena</strong>.</li>
        <li>Ako sliice nisu iste, smatra se da par nije pronaen. Sliice se ponovo skrivaju i igra gubi jedan pokuaj.</li>
      </ul>
      <p className={styles.instructionsParagraph}>
        Igra zavrava na jedan od sljedea dva naina:
      </p>
      <ul className={styles.instructionsParagraph}>
        <li>Igra pronae svih 8 parova  u tom sluaju dobija <strong>bonus po 1 poen</strong> za svaki neiskoriteni pokuaj.</li>
        <li>Igrau ponestane pokuaja  u tom sluaju ne dobija bonus, ve samo poene za parove koje je uspio pronai.</li>
      </ul>
    </div>
  );
};

const DesnoLijevoSection: React.FC = () => {
  return (
    <div className={styles.instructionsSection}>
      <h2 className={styles.subTitle}>Desno  Lijevo</h2>
      <p className={styles.instructionsParagraph}>
        Desno  Lijevo je igra u kojoj se od igraa trai da prepozna i odabere pojmove koji pripadaju zadatoj grupi. 
        Na poetku igre prikazuje se <strong>tema (opis grupe pojmova)</strong> i ukupno <strong>14 pojmova</strong>, od kojih tano <strong>7 pripada grupi</strong>, dok <strong>7 ne pripada</strong>.
      </p>
      <p className={styles.instructionsParagraph}>
        Igra ima <strong>50 sekundi</strong> da paljivo proita pojmove i odabere one koje smatra ispravnima.
      </p>
      <p className={styles.instructionsParagraph}>
        Bodovanje se vri na sljedei nain:
      </p>
      <ul className={styles.instructionsParagraph}>
        <li>Za svaki tano odabran pojam igra dobija <strong>2 poena</strong>.</li>
        <li>Ako igra tano identifikuje svih 7 pojmova, dobija <strong>bonus od 1 poena</strong>.</li>
      </ul>
    </div>
  );
};

const SefSection: React.FC = () => {
  return (
    <div className={styles.instructionsSection}>
      <h2 className={styles.subTitle}>Sef</h2>
      <p className={styles.instructionsParagraph}>
        Sef je igra logikog zakljuivanja u kojoj je cilj otkriti etvorocifrenu kombinaciju brojeva. Svaki broj u kombinaciji je iz skupa brojeva od 1 do 6, a brojevi se mogu ponavljati.
      </p>
      <p className={styles.instructionsParagraph}>
        Kombinacija brojeva je nasumino generisana i redoslijed brojeva u kombinaciji je vaan. Igra unosi svoje pokuaje da otkrije kombinaciju, a nakon svakog pokuaja dobija povratnu informaciju o tome:
      </p>
      <ul className={styles.instructionsParagraph}>
        <li>koliko brojeva je na tanoj poziciji,</li>
        <li>i koliko brojeva se nalaze u kombinaciji, ali na pogrenoj poziciji.</li>
      </ul>
      <p className={styles.instructionsParagraph}>
        Za pogaanje kombinacije igra ima <strong>90 sekundi</strong>.
      </p>
      <p className={styles.instructionsParagraph}>
        Svaki put kada igra potvrdi unesenu kombinaciju, prikazuju se etiri indikatora koji pomau u analizi pokuaja:
      </p>
      <ul className={styles.instructionsParagraph}>
        <li><strong>Crveni indikator</strong> oznaava broj koji je na pravoj poziciji,</li>
        <li><strong>uti indikator</strong> oznaava broj koji se nalazi u kombinaciji, ali nije na pravom mjestu,</li>
        <li><strong>Sivi indikator</strong> oznaava broj koji se ne nalazi u kombinaciji.</li>
      </ul>
      <p className={styles.instructionsParagraph}>
        Bodovanje se vri na sljedei nain:
      </p>
      <ul className={styles.instructionsParagraph}>
        <li><strong>15 poena</strong> za tano pogoenu kombinaciju,</li>
        <li><strong>5 poena</strong> za svaki neiskoriteni pokuaj koji je ostao nakon to je kombinacija pogoena.</li>
      </ul>
    </div>
  );
};

const ZidSection: React.FC = () => {
  return (
    <div className={styles.instructionsSection}>
      <h2 className={styles.subTitle}>Zid</h2>
      <p className={styles.instructionsParagraph}>
        Zid je igra opaanja i znanja u kojoj se iza 12 cigli krije poznata linost. Cilj igre je otkriti o kojoj linosti je rije tako to igra postepeno rui cigle odgovarajui na pitanja.
      </p>
      <p className={styles.instructionsParagraph}>
        Na svakoj cigli se nalazi jedno pitanje sa 4 ponuena odgovora. Igra klikom na ciglu otkriva pitanje, a zatim bira odgovor:
      </p>
      <ul className={styles.instructionsParagraph}>
        <li>Ako je odgovor taan, cigla se rui, otkriva se dio slike i igra dobija <strong>3 poena</strong>.</li>
        <li>Ako je odgovor netaan, cigla postaje blokirana i igra gubi <strong>1 poen</strong>.</li>
      </ul>
      <p className={styles.instructionsParagraph}>
        Igra se zavrava na jedan od dva naina:
      </p>
      <ul className={styles.instructionsParagraph}>
        <li>Ako igra tano pogodi o kojoj linosti se radi,</li>
        <li>Ili ako su sve cigle ili sruene ili blokirane.</li>
      </ul>
      <p className={styles.instructionsParagraph}>
        Ukoliko igra pogodi ko se krije iza zida, osim sakupljenih poena, dobija i <strong>bonus od 4 poena za svaku preostalu ciglu koja nije sruena niti blokirana</strong>.
      </p>
      <p className={styles.instructionsParagraph}>
        Ako ne pogodi, zadrava samo poene osvojene ruenjem cigli.
      </p>
      <p className={styles.instructionsParagraph}>
        Za igru igra ima <strong>180 sekundi</strong>.
      </p>
    </div>
  );
};

const PutOkoSvijetaSection: React.FC = () => {
  return (
    <div className={styles.instructionsSection}>
      <h2 className={styles.subTitle}>Put oko svijeta</h2>
      <p className={styles.instructionsParagraph}>
        Put oko svijeta je igra znanja u kojoj igra ima zadatak da prepozna drave na osnovu njihovih zastava.
      </p>
      <p className={styles.instructionsParagraph}>
        U svakoj rundi prikazuje se jedna zastava, a igra moe izabrati izmeu dvije opcije:
      </p>
      <ul className={styles.instructionsParagraph}>
        <li>Pokuati pogoditi naziv drave meu <strong>4 ponuena odgovora</strong>,</li>
        <li>Ili preskoiti trenutnu zastavu i prei na narednu.</li>
      </ul>
      <p className={styles.instructionsParagraph}>
        Sistem bodovanja funkcionie na sljedei nain:
      </p>
      <ul className={styles.instructionsParagraph}>
        <li>Taan odgovor donosi <strong>4 poena</strong>.</li>
        <li>Netaan odgovor oduzima <strong>2 poena</strong>.</li>
        <li>Preskakanje ne donosi niti oduzima poene.</li>
      </ul>
      <p className={styles.instructionsParagraph}>
        Igra traje ima tano <strong>10 rundi</strong>. 
      </p>
      <p className={styles.instructionsParagraph}>
        Ukupno vrijeme predvieno za igru je <strong>150 sekundi</strong>.
      </p>
    </div>
  );
};

const AsocijacijeSection: React.FC = () => {
  return (
    <div className={styles.instructionsSection}>
      <h2 className={styles.subTitle}>Asocijacije</h2>
      <p className={styles.instructionsParagraph}>
        U igri Asocijacije cilj je da se otkriju etiri pojma, kao i konano rjeenje koje povezuje sve prethodne pojmove.
      </p>
      <p className={styles.instructionsParagraph}>
        Tabla je podijeljena na etiri kolone, a svaka kolona sadri etiri polja sa pojmovima. Igra moe redom otkrivati polja kako bi pronaao zajedniku nit koja povezuje pojmove u koloni.
      </p>
      <p className={styles.instructionsParagraph}>
        Kada igra smatra da zna rjeenje odreene kolone, moe ga unijeti runo. Isto vai i za konano rjeenje koje povezuje sve etiri kolone.
      </p>
      <p className={styles.instructionsParagraph}>
        Bodovanje u igri funkcionie na sljedei nain:
      </p>
      <ul className={styles.instructionsParagraph}>
        <li>Tano rjeenje jedne kolone donosi <strong>5 poena</strong> i bonus <strong>po 1 poen</strong> za svako neoktriveno polje u koloni.</li>
        <li>Tano konano rjeenje donosi dodatnih <strong>12 poena</strong>, bonus <strong>6 poena</strong> za svako neoktriveno rjeenje kolone i bonus <strong>po 1 poen</strong> za svako neoktriveno polje u kolonama.</li>
      </ul>
      <p className={styles.instructionsParagraph}>
        Ukupno vrijeme predvieno za igru je <strong>180 sekundi</strong>.
      </p>
    </div>
  );
};

const PremetaljkaSection: React.FC = () => {
  return (
    <div className={styles.instructionsSection}>
      <h2 className={styles.subTitle}>Premetaljka</h2>
      <p className={styles.instructionsParagraph}>
        U igri Premetaljka igrau se prikazuju tri rijei ija su slova nasumino ispremetana. Cilj igre je da igra pravilno premetne slova kako bi otkrio originalne rijei.
      </p>
      <p className={styles.instructionsParagraph}>
        Kada igra uspjeno pogodi rije, slova te rijei pozelenjuju, ime se potvruje taan odgovor.
      </p>
      <p className={styles.instructionsParagraph}>
        Za svako tano pronaeno slovo igra dobija <strong>1 poen</strong>.
      </p>
      <p className={styles.instructionsParagraph}>
        Ukupno vrijeme predvieno za ovu igru je <strong>180 sekundi</strong>.
      </p>
    </div>
  );
};


export default Instructions;
